export default {
//  
  copyObject(p, c) {
    c = c || {}
    for (var i in p) {
      //  ip
      if (p.hasOwnProperty(i)) {
        //  i
        if (typeof p[i] === 'object') {
          // p[i]
          // p[i]
          c[i] = Array.isArray(p[i]) ? [] : {}
          //  
          this.copyObject(p[i],c[i])
        }else {
        //  
          c[i] = p[i]
        }

      }
    }
    return c
  },
//  
  checkPhoneNo(x) {
    if(!(/^1[34578]\d{9}$/.test(x))){
      return false
    }else{
      return true
    }
  },
//  
  checkPassword(x) {
    if(x.length < 6 || x.length > 20){
      return true
    }
    //
    if(/.*[\u4e00-\u9fa5]+.*$/.test(x)){
      return true
    }
  //  
    if(/^[0-9]*$/.test(x)){
      return true
    }
  //  
    if(/^[A-Za-z]+$ /.test(x)){
      return true
    }

    //
    if(/\d+/.test(x)){
      //    
      return false
    }else if(/.*[A-Za-z]+.*$/.test(x)){
      //    
      return false
    }else{
      return true
    }



  }
}



